.page {
  height: 100%;

  &__body {
    display: grid;
    grid-template-rows: min-content 1fr min-content;
    align-content: start;
    min-width: 320px;
    min-height: 100%;
    max-width: 1440px;
    margin: 0 auto;
    background-color: @pale-orange;
    color: @black;

    font-size: 13px;
    font-family: "Raleway", sans-serif;
    font-weight: 300;
    line-height: 17px;
    text-align: center;
  }
}
