.carousel-indicators {
  bottom: -42px;
}


.carousel-indicators [data-bs-target] {
  align-self: center;
  width: 9px;
  height: 9px;
  border-radius: 50%;
  background-color: @dark-grayish;
}

.carousel-indicators .active {
  background-color: @pale-orange;
  box-sizing: border-box;
  border: 1px solid @black;
}
